<template>
    <div class="luckyinfo">
        <div class="bg"></div>
        <div class="tip">{{tip}}</div>
        <div class="gift"></div>
        <div class="tip_msg"></div>
        <div class="tipMsg">
            7
        </div>
        <div class="btn">
            <van-button :disabled="disable" :type="type" round @click="dream">{{btntxt}}</van-button>
            <p @click="lookpress"></p>
        </div>
        <popup :show="isshow" :title="title" :did="did" :content="content" :state="status"/>
    </div>
</template>

<script>
import popup from '@/components/dream/add/Add'
export default {
    name:'luckyinfo',
    data () {
        return {
            tip:'',
            show:true,
            disable:false,
            btntxt:'',
            state:'',
            type:'',
            isshow:false,
            title:'',//
            did:'',// id,
            content:'',//
            status:'all'
        }
    },
    components:{
        popup
    },
    created(){
        if(this.$route.params == {}){
            this.$router.push('/')
        }else{
            this.state = this.$route.params.state
            if(this.$route.params.state == 'SUCCESS'){
                this.disable = true
                this.type = 'primary'
                this.btntxt = ''
                this.show = false
            }else if(this.$route.params.state == 'DOING'){
                this.btntxt = ''
                this.type = 'primary'
            }else if(this.$route.params.state == 'VERIFY'){
                this.disable = true
                this.btntxt = ''
                this.type = 'warning'
            }
            this.tip = ''+this.$route.params.ptitle+''+this.$route.params.lid+''+this.$route.params.bill+''
        }
    },
    methods:{
        // 
        dream(){
            this.perfect(this)
        },
        // 
        perfect(app){
            TD_Request('us','rnamegx',{uid:app.$store.state.uid},function(code,data){
                app.isshow = true;
                app.title = app.$route.params.title
                app.content = app.$route.params.content
                app.did = app.$route.params.did
            },function(code,data){
                if(code == 12){
                    app.$dialog.confirm({
                        title:'',
                        message:'',
                        confirmButtonText:'',
                        cancelButtonText:'',
                        confirmButtonColor:'#00d094'
                    }).then(() => {
                        app.$router.push('/auth/lucky')
                    }).catch(()=>{
                        return;
                    })
                }
            })
        },
        lookpress(){
            
        },
    },
    watch:{
        $route(to,from){
            this.perfect(this);
        }
    }
}
</script>

